import React from "react";
import "./App.css";

const table = [
  { rank: 1, name: "Tomer", points: 22.8 },
  { rank: 2, name: "Shapira", points: 16.2 },
  { rank: 3, name: "Kashi", points: 16.1 },
  { rank: 4, name: "Fleischer", points: 15.2 },
  { rank: 5, name: "Eldad", points: 14 },
  { rank: 6, name: "Shayko", points: 13.6 },
  { rank: 7, name: "Yona", points: 13.1 },
  { rank: 8, name: "Shahak", points: 12.9 },
  { rank: 9, name: "Nave", points: 10.8 },
  { rank: 10, name: "Kasman", points: 10.2 },
  { rank: 11, name: "Shkedi", points: 9.8 },
  { rank: 12, name: "JZ", points: 9.1 },
  { rank: 13, name: "SachiÃ±o", points: 7.8 },
];

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      table: table,
      lastUpdated: "",
    };
  }

  renderTableData() {
    return this.state.table.map((person, index) => {
      const { rank, name, points } = person;
      return (
        <tr key={rank}>
          <td>{rank}</td>
          <td>{name}</td>
          <td>{points}</td>
        </tr>
      );
    });
  }

  renderTableHeader() {
    let header = Object.keys(this.state.table[0]);
    return header.map((key, index) => {
      return <th key={index}>{key.toUpperCase()}</th>;
    });
  }

  render() {
    return (
      <div>
        <h1 id="title">NBA Playoffs Table</h1>
        <table id="table">
          <tbody>
            <tr>{this.renderTableHeader()}</tr>
            {this.renderTableData()}
          </tbody>
        </table>
        <h2 id="secondTitle">Last update: 03.09.2020 @ 12:40</h2>
      </div>
    );
  }
}

export default App;
